<template>
  <div>
    <router-view></router-view>
    <el-container>
      <el-main class="main">
        <img src="./assets/logoko.png" />
        <h1 v-show="show" class="animated fadeInUp">1995-01-07</h1>
        <h1 v-show="show" class="animated fadeInUp">HeFei<i class="rhombus"></i>China</h1>
        <main-canvas :dotsNum="dotsNum" ></main-canvas>
        
      </el-main>
    </el-container>
  </div>
</template>

<script>
import mainCanvas from './components/mainCanvas'
export default {
  name: 'app',
  components: {
    mainCanvas
  },
  mounted: function () {
    this.show = true
  },
  data () {
    return {
      dotsNum: 60,
      roundColor: '#999',
      lineColor: '',
      isColor: true,
      show: false
    }
  }
}
</script>

<style lang="scss">
@import "./style/entry";

.main {
  text-align: center;
  box-sizing: border-box;
  padding-top: 16%;
}

img {
  width: 2.8rem;
  margin: auto;
}

h1 {
  text-align: right;
  width: 2rem;
  margin: auto;
  font-size: 0.2rem;
  line-height: .3rem;
}

.rhombus {
  display: inline-block;
  margin: 0  .08rem .03rem .08rem;
  border-style: solid;
  border-width: .03rem;
  width: 0;
  height: 0;
  -moz-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.animated{
  animation-duration: 2s;
  -webkit-animation-duration: 2s;
}
</style>



