import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'

import App from '@/components/app/App'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main,
      children: [
        {
          path: 'app',
          name: 'app',
          component: App
        }
      ]
    }
  ]
})



